@import url("Fonts.css");

body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  background-color: rgb(0, 0, 0);
  font-size: 20px;
  scroll-behavior: smooth;
  /* font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif */
  /* font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif */
}

.icon-format {
  height: 50px;
  width: auto;
}

.margin-icon-container {
  margin-bottom: 30px; 
}

.margin-icons {
  margin-left: 10px;
  margin-right: 10px;
}

.text {
  color: white;
}

.bgskills {
  border-radius: 50px 50px 0px 0px;
  border-bottom: solid 1px ;
  background-color: #ffffff;
}

.flex-column-center {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.flex-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.flex-row-center {
  justify-content: center;
}

.flex-sp-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.centered-text {
  text-align: center;
}

/* ======= Hero ======= */

.hero {
  height: 100vh;
  width: 100vw;
  display: grid;
  overflow: hidden;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(6, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 10px;
  }

.heroRow1 { 
    display: flex;
    justify-content: flex-start;
    align-items: center;
    overflow: hidden;
    font-family: "oswald";
    font-size: 5.5rem;
    font-weight: 600;
    text-transform: uppercase;
    color: #ffffff;
    grid-area: 2 / 2 / 3 / 12; 
    background-color: 1px solid red;
  }

  .toggleDiv1{ 
    display: flex;
    flex-direction: column;
    position: relative;
    top: 70%;
  }

  .heroRow2 { 
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    font-family: "oswald";
    font-size: 5.5rem;
    font-weight: 600;
    text-transform: uppercase;
    color: #ffffff;
    grid-area: 3 / 2 / 4 / 12; 
  }

.heroRow3 { 
    display: flex;
    justify-content: flex-end;
    align-items: center;
    overflow: hidden;
    font-family: "oswald";
    font-size: 5.5rem;
    font-weight: 600;
    text-transform: uppercase;
    color: #ffffff;
    grid-area: 4 / 2 / 5 / 12; 
  }

  .toggleDiv2 { 
    display: flex;
    flex-direction: column;
    position: relative;
    top: 70%;
  }

  .thumbnail1, .thumbnail2, .thumbnail3 {
    width: auto;
    height: 12vh;
    margin-top: 0.7%; 
    margin-right: 2%; 
    margin-left: 2%;
    border-radius: 10%;
  }
  
.arrowdown {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-area: 6 / 2 / 7 / 12;   
  }

/* ======= Hello ======= */

.hello {
    height: 60vh;
    width: 100vw;
    overflow: hidden;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(12, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    position: relative;
    z-index: -1;
  }
  
.div2 { 
  font-size: 21rem;
  font-weight: 900;
  width: 300vw;
  color: #ffffff;
  position: relative;
  left: 0;
  grid-area: 0 / 1 / 12 / 12; 
}

/* ======= Banners ======= */

.banner1 {
  height: 20vh;
  width: 100vw;
  overflow: hidden;
  position: relative;
  z-index: -1;
}

.banner1 > .deel1 { 
  width: 100vw;
  font-family: "SUSE";
  font-size: 6rem;
  font-weight: 500;
  color: #ffffff;
  display: flex;
  justify-content: space-around;
  background-color: red; 
}

.banner1 > .deel2 { 
  width: 100vw;
  font-family: "SUSE";
  font-size: 6rem;
  font-weight: 500;
  color: #ffffff;
  display: flex;
  justify-content: space-around;
  position: absolute;
  left: 100%;
  top: 0;
  background-color: rgb(4, 0, 255); 
}

.banner2 {
  height: 20vh;
  width: 100vw;
  overflow: hidden;
  position: relative;
  z-index: -1;
}

.banner2 > .deel1 { 
  width: 100vw;
  overflow: hidden;
  font-family: "SUSE";
  font-size: 6rem;
  font-weight: 500;
  color: #ffffff;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: red; 
}

.banner2 > .deel2 { 
  width: 100vw;
  overflow: hidden;
  font-family: "SUSE";
  font-size: 6rem;
  font-weight: 500;
  color: #ffffff;
  display: flex;
  justify-content: space-around;
  position: absolute;
  left: 100%;
  top: 0;
  background-color: rgb(4, 0, 255); 
}

.smile {
  width: 6vw;
}
/* ======= about me ======= */

.about {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(12, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  position: relative;
  z-index: 1;
 
}

.about > .div1 {
    font-family: "SUSE";
    font-size: 2.5rem;
    font-weight: 700;
    color: #ffffff;
    grid-area: 4 / 2 / 12 / 6;
    /* border: 1px solid red */
}

.img {
    position: relative;
    grid-area: 1 / 6 / 13 / 13;  
    z-index: 5;
    /* border: 1px solid red */
}

.photo {
    position: absolute;
    width: 25%; 
    z-index: 5;
}
.cirkel {
    position: absolute;
    width: 25%; 
    z-index: 5;
}

